import { useState, useEffect, FC } from 'react';
import Slide from './Slide';
// import { ISupplierBook } from '../interfaces/ISupplierBook.interface';

interface ISliderProps {
  entryCount: number;
  entries: {
    title: string;
    authors: string[];
    image: string;
  }[];
}

const Slider: FC<ISliderProps> = ({ entryCount, entries }) => {
  const [index, setIndex] = useState(0);
  // eslint-disable-next-line
  const [timer, setTimer] = useState<any>();
  const delay = 2200;

  useEffect(() => {
    // eslint-disable-next-line
    timer && clearTimeout(timer);

    const newTimer = setTimeout(() => {
      const changeIndex = () => {
        if (index === entryCount - 1) return setIndex(0);
        return setIndex((old) => old + 1);
      };
      changeIndex();
    }, delay);
    setTimer(newTimer);
    // eslint-disable-next-line
  }, [index]);

  function changeSlide(dir = 0) {
    const newIndex = index + dir;

    if (newIndex >= entryCount) return setIndex(0);
    if (newIndex <= -1) return setIndex(entryCount - 1);

    return setIndex(newIndex);
  }

  return (
    <div className="flex place-content-start mt-2 ml-6">
      <div className="flex flex-col items-center bg-gray-50 shadow p-2">
        <Slide title={entries[index]?.title} author={entries[index]?.authors?.join(' ')} image={entries[index]?.image} />
        <div className="flex flex-row">
          <button
            className="flex px-2 py-1 border-gray-400 text-gray-400 rounded-sm border-2 max-h-full
            transition duration-500 ease-in-out hover:bg-gray-400 hover:text-gray-50"
            type="button"
            onClick={() => changeSlide(-1)}
          >
            ⮜
          </button>
          <div className="w-80" />
          <button
            className="flex px-2 py-1 border-gray-400 text-gray-400 rounded-sm border-2 max-h-full
            transition duration-500 ease-in-out hover:bg-gray-400 hover:text-gray-50"
            type="button"
            onClick={() => changeSlide(1)}
          >
            ⮞
          </button>
        </div>
      </div>
    </div>
  );
};

export default Slider;
